export function text(element) {

}

export function number(element, 
        config = { axis: 'x', inital: 0, doubleClickReset: true}) {
    let listeners = [(v) => element.textContent = v];
    let frameId = null;
    function positionOnAxis(el) {
        return ('x' === axis) ? el.clientX : el.clientY;
    }

    function listen(f) {
        listeners.push(f);
        return () => { listeners = listeners.filter(x => x !== f) }
    }

    function set(x) {
        console.log('set')
        if (frameId !== null) cancelAnimationFrame(frameId)
        if (listeners.length === 0) return;

        frameId = requestAnimationFrame(() => {
            frameId = null;
            console.log('notify')
            listeners.forEach(l => l(x))
        });
    }

    function handleClick(ev) {
        let inital = positionOnAxis(ev)
        let handler = window.addEventListener('mousemove', (ev) => {
            let pos = positionOnAxis(ev)
            set(pos)
        });
        window.addEventListener('mouseup', () => {
            window.removeEventListener("mousemove", handler);
        });
    }

    element.addEventListener('onclick', handleClick);
    set(config.inital);

    let ob = { listen, set }
    return ob
}